

*{margin: 0; padding: 0;}

/* Nav */
.nav-wrap{margin: 40px 0;}
.nav-title{}
.nav-count{}


/* Input Form */
.input-form{margin-bottom: 30px;}
.todo-input{
  width: 150px;
  height: 30px;
  padding: 3px 3px 3px 6px;
  border-radius: 10px;
  border: 1px solid #454545;
  outline: none;}

.add-button{
  width: 50px;
  height: 36px;
  margin-left: 20px;
  border-radius: 10px;
  background-color: #e4e4e4;
  transition: 500ms;
}
.add-button:hover{
  background-color: #a2a2a2;
}

/* Todo List */
.todo{
  font-size: 1.5rem;
  list-style: none;
  margin-bottom: 20px;
}

/* Todo Text */
.todo-count{
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  color: #fff;
  background-color: #34a853;
  border-radius: 50%;
  margin: 0 20px;
  transition: 500ms;
}
.todo-count:hover{
  opacity: 0.8;
}

/* Todo button */
button{
  outline: none;
  border: 0;
  cursor: pointer;
}
.todo-button{
  background: transparent;
  font-size: 1.5rem;
  margin: 0 5px;
  transition: 500ms;
}
.todo-button:hover{
  opacity: 0.8;
}
.todo-increase,
.todo-decrease{
  color: #34a853;
}
.todo-delete{
  color: #ea4335;
}

/* Todo Reset */
button.resetButton{
  width: 100px;
  height: 30px;
  border-radius: 10px;
  color: #fff;
  background-color: #ea4335;
  opacity: 0.5;
  transition: 500ms;
}
button.resetButton:hover{
  opacity: 1;
}